import React, { Component } from "react";
import CardLayout from "../../components/Cards/CardLayout";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import { FormGroup, Col, Button } from "reactstrap";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
import { Link } from "react-router-dom";
import Loader from "../../components/Loader/Loader";
import DateRangePicker from "react-bootstrap-daterangepicker";
import "bootstrap-daterangepicker/daterangepicker.css";
import moment from "moment";
class FeedbackResponses extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      userFeedbackList: [],
      dateFilter: false,
      startDate: "",
      endDate: "",
      calenderStart: "",
      calenderEnd: ""
    };
  }
  componentDidMount() {
    this.props.getUserFeedbackList();
    let compRef = this;
    setTimeout(() => {
      compRef.setState({
        loading: false,
        userFeedbackList: this.props.userFeedbacks,
        calenderStart: moment(new Date()).format("M/D/YYYY"),
        calenderEnd: moment(new Date())
          .add(1, "M")
          .format("M/D/YYYY")
      });
    }, 2000);
  }

  onDeleteState(cell, row) {
    return (
      <Link to={this} style={{ pointerEvents: "none", opacity: 0.5 }}>
        <i className="fa fa-trash" title="Delete" />
      </Link>
    );
  }

  onEditState(cell, row) {
    return (
      <Link to={this} style={{ pointerEvents: "none", opacity: 0.5 }}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  showRatingStars(cell, row) {
    if (cell === 3) {
      return <i className="fa fa-frown-o feedback-icon" title="Sad" />;
    } else if (cell === 2) {
      return <i className="fa fa-meh-o feedback-icon" title="Neutral" />;
    } else if (cell === 1) {
      return <i className="fa fa-smile-o feedback-icon" title="Happy" />;
    } else {
      return;
    }
  }
  dateFormat(cell, row) {
    return cell ? moment(cell).format("DD-MM-YYYY") : "";
  }
  handleEvent(event, picker) {
    let startDate = moment(picker.startDate._d).format("DD-MM-YYYY");
    let endDate = moment(picker.endDate._d).format("DD-MM-YYYY");
    let feedbackResponses = [...this.state.userFeedbackList];
    let userFeedbackList = [];
    this.props.userFeedbacks.forEach(feedback => {
      if (
        moment(feedback.CreatedOn).isBetween(
          moment(picker.startDate._d),
          moment(picker.endDate._d)
        ) ||
        moment(feedback.CreatedOn).isSame(moment(picker.startDate._d)) ||
        moment(feedback.CreatedOn).isSame(moment(picker.endDate._d))
      ) {
        userFeedbackList.push(feedback);
      }
    });
    this.setState({
      userFeedbackList: userFeedbackList,
      startDate: startDate,
      endDate: endDate,
      dateFilter: true
    });
  }
  onReset() {
    this.setState({
      userFeedbackList: this.props.userFeedbacks,
      startDate: "",
      endDate: "",
      dateFilter: false,
      calenderStart: moment(new Date()).format("M/D/YYYY"),
      calenderEnd: moment(new Date())
        .add(1, "M")
        .format("M/D/YYYY")
    });
  }
  render() {
    const {
      startDate,
      endDate,
      dateFilter,
      calenderStart,
      calenderEnd
    } = this.state;
    const sortingOptions = {
      defaultSortName: "CreatedOn",
      noDataText: "No records found for feedback response",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.userFeedbacks.length
        }
      ],
      sizePerPage: 5
    };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout name="Feedback Response">
        <FormGroup row>
          {/* <Col xs="12" md="8" /> */}
          <Col xs="12" md="2">
            <DateRangePicker
              startDate={calenderStart}
              endDate={calenderEnd}
              onApply={this.handleEvent.bind(this)}
              onCancel={this.onReset.bind(this)}
            >
              <Button className="theme-positive-btn ">Apply Date Filter</Button>
            </DateRangePicker>
          </Col>
          <Col md="1">
            <Button
              className="theme-reset-btn"
              onClick={this.onReset.bind(this)}
            >
              Reset
            </Button>
          </Col>
          <Col md="6" />
          <Col md="3">
            {dateFilter ? (
              <div className="help-block">
                Date Range : {startDate} - {endDate}
              </div>
            ) : null}
          </Col>
        </FormGroup>
        <FormGroup row>
          <Col xs="12">
            <BootstrapTable
              ref="table"
              data={this.state.userFeedbackList}
              pagination={this.state.userFeedbackList.length > 0 ? true : false}
              search={true}
              options={sortingOptions}
              //exportCSV={true}
              hover={true}
              //csvFileName="Crops List"
            >
              <TableHeaderColumn
                dataField="userId"
                headerAlign="left"
                isKey
                hidden
              >
                Id
              </TableHeaderColumn>
              <TableHeaderColumn
                dataField="Name"
                headerAlign="left"
                width="40"
                dataSort={true}
              >
                Beneficiary
              </TableHeaderColumn>
              <TableHeaderColumn
                dataField="FeedBack"
                headerAlign="left"
                width="30"
                dataFormat={this.showRatingStars.bind(this)}
                dataSort={true}
              >
                Rating
              </TableHeaderColumn>

              <TableHeaderColumn
                dataField="CreatedOn"
                headerAlign="left"
                dataFormat={this.dateFormat.bind(this)}
                width="20"
                export={false}
              >
                Date
              </TableHeaderColumn>
            </BootstrapTable>
          </Col>
        </FormGroup>
      </CardLayout>
    );
  }
}
const mapStateToProps = state => {
  return {
    userFeedbacks: state.feedbackReducer.userFeedbacks
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getUserFeedbackList: () => dispatch(actions.getUserFeedbackList())
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(FeedbackResponses);



