import React, { Component } from "react";
import CardLayout from "../../components/Cards/CardLayout";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import { FormGroup, Col,  Row } from "reactstrap";
import DropdownSelect from "../../components/InputElement/Dropdown";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
import { Link } from "react-router-dom";
import Loader from "../../components/Loader/Loader";
import ConfirmModal from "../../components/Modal/ConfirmModal";
import * as constants from "../../constants/StatusConstants";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import ActiveQuestionTable from "./ActiveQuestionTable";
import InActiveQuestionTable from "./InActiveQuestionTable";

class FeedbackQuestions extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tableStatus: true,
      loading: true,
      questionToDelete: {},
      modalStatus: false
    };
  }
  componentDidMount() {
    this.props.getFeedbackQuestionList();
    let compRef = this;
    setTimeout(() => {
      compRef.setState({
        loading: false
      });
    }, 1000);
  }

  onDeleteQuestion(cell, row) {
    if(this.state.tableStatus){
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    }else{
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
    
  }
  onModalToggle(question) {
    this.setState({
      questionToDelete: question,
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
    let questionToDelete = { ...this.state.questionToDelete };
    let displayMessage = "";
    if(this.state.tableStatus){
      questionToDelete.Active = false;
      displayMessage = "Question deactivated successfully"
    }else{
      questionToDelete.Active = true;
      displayMessage = "Question activated successfully"
    }
    this.props.deleteFeedbackQuestion(questionToDelete.Id, questionToDelete);
    this.setState({ loading: true });
    setTimeout(() => {
      let message = "";
      this.props.feedbackQuestionError
        ? (message = "Something went wrong !")
        : (message = displayMessage);
      this.setState({ loading: false });
      Toaster.Toaster(message, this.props.feedbackQuestionError);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  onEditQuestion(cell, row) {
    return (
      <Link to={`${this.props.match.path}/QuestionForm/${row.Id}`}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  render() {
   
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Feedback Questions"
        //buttonName={
        ///  this.props.activeQuestionsList.length === 0 ? "Add Question" : null
        //}
        buttonName="Add Question"
        buttonLink={`${this.props.match.url}/QuestionForm`}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              simpleValue
              clear ={false}
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActiveQuestionTable
                feedbackQuestions={this.props.activeQuestionsList}
                onEdit={this.onEditQuestion.bind(this)}
                onDelete={this.onDeleteQuestion.bind(this)}
              />
            ) : (
              <InActiveQuestionTable
                feedbackQuestions={this.props.inActiveQuestionsList}
                onEdit={this.onEditQuestion.bind(this)}
                onDelete={this.onDeleteQuestion.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={"Deactivate"}
          message="Are you sure you want to deactivate this question ?"
        />
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}
const mapStateToProps = state => {
  return {
    activeQuestionsList: state.feedbackReducer.activeQuestionsList,
    inActiveQuestionsList: state.feedbackReducer.inActiveQuestionsList,
    feedbackQuestionError: state.feedbackReducer.feedbackQuestionError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getFeedbackQuestionList: () => dispatch(actions.getFeedbackQuestionList()),
    deleteFeedbackQuestion: (id, question) =>
      dispatch(actions.deleteFeedbackQuestion(id, question))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(FeedbackQuestions);



