import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import CardLayout from "../../components/Cards/CardLayout";
import { FormGroup, Col, Button } from "reactstrap";
import InputElement from "../../components/InputElement/InputElement";
import _ from "lodash";
import Loader from "../../components/Loader/Loader";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
class FeedbackQuestionForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      updateFlag: false,
      feedbackQuestion: {
        Id: "",
        Questions: "",
        QuestionsRequired: false,
        CreatedBy: "",
        CreatedOn: "",
        UpdatedBy: "",
        UpdatedOn: "",
        Active: true,
        QuestionsInvalid : false
      }
    };
  }

  componentDidMount() {
    if (this.props.match.params.id !== undefined) {
      this.props.getFeedbackQuestionList();
      setTimeout(() => {
        let currentQuestion = _.find(this.props.activeQuestionsList, {
          Id: parseInt(this.props.match.params.id)
        });
        this.setState({
          updateFlag: true,
          feedbackQuestion: currentQuestion,
          loading: false
        });
      }, 1000);
    }else{
      this.setState({
        loading: false
      });
    }
  }

  onChangeHandler(event) {
    let feedbackQuestion = { ...this.state.feedbackQuestion };
    feedbackQuestion[event.target.name] = event.target.value;
    feedbackQuestion[event.target.name + "Required"] = false;
    feedbackQuestion[event.target.name + "Invalid"] = false;    
    this.setState({ feedbackQuestion });
  }

  onSubmit() {
    let feedbackQuestion = { ...this.state.feedbackQuestion };
    let compRef = this;
    if (feedbackQuestion.Questions && feedbackQuestion.Questions.trim().length > 0) {
      feedbackQuestion.Active = true;
      let questionUpdate = _.pick(feedbackQuestion, [
        "Id",
        "Questions",
        "UpdatedOn",
        "UpdatedBy",
        "Active"
      ]);
      let question = _.pick(feedbackQuestion, [
        "Questions",
        "CreatedBy",
        "CreatedOn",
        "Active"
      ]);

      questionUpdate.UpdatedOn = new Date();
      questionUpdate.UpdatedBy = localStorage.getItem("user");
      question.CreatedOn = new Date();
      question.CreatedBy = localStorage.getItem("user");
      if (this.state.updateFlag) {
        this.props.updateFeedbackQuestion(questionUpdate.Id, questionUpdate);
      } else {
        this.props.createFeedbackQuestion(question);
      }
      this.setState({ loading: true });
      setTimeout(() => {
        let message = "";
        compRef.props.questionError
          ? (message = "Something went wrong !")
          : compRef.state.updateFlag
            ? (message = "Question updated successfully")
            : (message = "Question created successfully");
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.props.questionError);
        setTimeout(() => {
          if (!compRef.props.questionError) {
            compRef.onReset();
            compRef.props.history.push("/feedback/feedbackQuestions");
          }
        }, 1000);
      }, 1000);
    } else {
      if(!feedbackQuestion.Questions) feedbackQuestion.QuestionsRequired = true;
      if(feedbackQuestion.Questions && feedbackQuestion.Questions.trim().length === 0) feedbackQuestion.QuestionsInvalid = true;
      this.setState({ feedbackQuestion });
    }
  }

  onReset() {
    this.setState({
      feedbackQuestion: {
        Id: "",
        Questions: "",
        QuestionsRequired: false,
        QuestionsInvalid: false,
        CreatedBy: "",
        CreatedOn: "",
        UpdatedBy: "",
        UpdatedOn: "",
        Active: true
      }
    });
  }
  render() {
    const feedbackQuestion = { ...this.state.feedbackQuestion };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Feedback Question Form"
        navigation={true}
        navigationRoute="/feedback/feedbackQuestions"
      >
        <div className="div-padding">
          <FormGroup row />
          <FormGroup row>
            <Col xs="8" md="4">
              <InputElement
                type="text"
                label="Question"
                placeholder="Question"
                maxLength={250}
                name="Questions"
                required={feedbackQuestion.QuestionsRequired}
                invalid={feedbackQuestion.QuestionsInvalid}
                value={feedbackQuestion.Questions}
                onChange={event => this.onChangeHandler(event)}
              />
            </Col>
          </FormGroup>
          {this.state.updateFlag ? (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={this.onSubmit.bind(this)}
                >
                  Save
                </Button>
              </Col>
            </FormGroup>
          ) : (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={this.onSubmit.bind(this)}
                >
                  Create
                </Button>
              </Col>
              <Col md="1">
                <Button
                  className="theme-reset-btn"
                  onClick={this.onReset.bind(this)}
                >
                  Reset
                </Button>
              </Col>
            </FormGroup>
          )}

          <ToastContainer autoClose={1000} />
        </div>
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    feedbackQuestionError: state.feedbackReducer.feedbackQuestionError,
    activeQuestionsList: state.feedbackReducer.activeQuestionsList
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getFeedbackQuestionList: () => dispatch(actions.getFeedbackQuestionList()),
    createFeedbackQuestion: question =>
      dispatch(actions.createFeedbackQuestion(question)),
    updateFeedbackQuestion: (id, question) =>
      dispatch(actions.updateFeedbackQuestion(id, question))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(
  FeedbackQuestionForm
);



