import React, { Component } from "react";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
class InActiveQuestionsTable extends Component {
  render() {
    const sortingOptions = {
      defaultSortName: "Questions",
      noDataText: "No records found for feedback questions",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.feedbackQuestions.length
        }
      ],
      sizePerPage: 5
    };
    return (
      <BootstrapTable
        ref="table"
        data={this.props.feedbackQuestions}
        options={sortingOptions}
        search={true}
        pagination = {this.props.feedbackQuestions.length > 0 ? true : false}
        hover={true}
      >
        <TableHeaderColumn dataField="Id" headerAlign="left" isKey hidden>
          Id
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="Questions"
          headerAlign="left"
          width="40"
          csvHeader="Crop Name"
          dataSort={true}
        >
          Question
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="delete"
          dataFormat={this.props.onDelete}
          headerAlign="left"
          width="20"
          export={false}
        >
          Activate
        </TableHeaderColumn>
      </BootstrapTable>
    );
  }
}

export default InActiveQuestionsTable;



